<template>
  <div class="home-page">
    <NavBar>
      <template slot="title">Chi Tiết</template>
    </NavBar>

    <div class="content container-fluid">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-3">
          
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6">
          <Tin :item="tin" />
        </div>
        <div class="col-sm-12 col-md-3">
          
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar.vue";
import Tin from "@/components/Tin.vue";
import _ from "lodash";

export default {
  name: "detail",
  components: {
    NavBar,
    Tin
  },
  computed: {
    tin(){
      return this.$store.getters['Tin/itemTemp']
    },
    tins(){
      return this.$store.getters['Tin/items']
    },
  },
  mounted: function() {
    var vm = this;
    var id = vm.$router.currentRoute.params.id;
    if(vm.tins.length){
      this.$store.commit('Tin/GET', id)
    }
    else{
      if(id) this.$store.dispatch('Tin/set', id)
    }
    
  }
};
</script>
